<template>
	<div class="order-detail-container">
		<topNav class="topNav" :title="title" :isleftarrow="isleftarrow"></topNav>
		<div class="mheader">
			<van-icon name="orders-o" class="iconfont" />
			<div class="mheader-status">
				{{ orderInfo.statusStr }}
			</div>
		</div>
		<div class="steps">
			<van-steps v-if="orderInfo.status > 0" :active="active" active-color="#38f" active-icon="success">
				<van-step v-for="(v,i) in steps" :key="i">
					{{v}}
				</van-step>
			</van-steps>
			<van-notice-bar v-if="orderInfo.status == -1" color="red" left-icon="close">
				订单已关闭
			</van-notice-bar>
		</div>

		<div class="user">
			<van-icon name="location" class="iconfont" />
			<div class="user-info">
				<div class="user-info-top">
					<div class="user-linkMan">
						{{ logistics.linkMan }}
					</div>
					<div class="user-mobile grap-color">
						{{ logistics.mobile }}
					</div>
				</div>
				<div class="user-info-bottom">
					<div class="user-addr">
						{{ logistics.provinceStr}}
						{{ logistics.cityStr }}
						{{ logistics.address }}
					</div>
				</div>
			</div>
		</div>
		<div class="deliver" v-for="(dItem,dIndex) in deliver" :key="dIndex">
			<van-icon name="logistics" class="iconfont"/>
			<div class="user-info">
				<div class="user-info-top">
					<div class="user-linkMan">
						{{ dItem.shipperName }}
					</div>
					<div class="user-mobile grap-color">
						{{ dItem.shipperCode }}
					</div>
				</div>
				<div class="user-info-bottom">
					<div class="user-addr" @click="handleClipboard(dItem.trackingNumber,$event)">
						订单号：{{dItem.trackingNumber}}
					</div>
				</div>
			</div>
		</div>
		<div class="main">
			<div class="group balance-group">
				<van-cell icon="shop-o" :value="orderInfo.shopName" is-link />
				<van-row class="group-inner">
					<van-card :num="item.number" :price="item.amountSingle" :desc="item.property" :title="item.goodsName" :thumb="item.pic"
					 v-for="(item,index) in good" :key="item.id">
						<template #footer>
							<van-button v-if="orderInfo.status == 3" round size="small">申请售后</van-button>
						</template>
					</van-card>
					<van-cell class="yunfei" title="运费" :value="'￥'+orderInfo.trips" />
					<van-cell class="shifu" title="实付款" :value="'￥'+orderInfo.amount" />
					<div class="block-20"></div>
					<van-cell class="shifu" title="订单编号" :value="orderInfo.orderNumber" />
					<van-cell class="shifu" v-for="(orderLogItem,orderLogIndex) in logs" :title="orderLogItem.typeStr" :value="addLogs.dateAdd" :key="orderLogIndex"/>
				</van-row>
			</div>
			<van-button v-if="orderInfo.status===2" type="info" block @click="comfirmReceipt">确认收货</van-button>
		</div>
	</div>
</template>

<script>
	import util from '@/common/util/util'
	import topNav from '../../components/topNav.vue'
	import clipboard from '../../common/clipboard/clipboard.js'
	export default {
		data() {
			return {
				goodsId: [],
				orderId: '',
				list: [],
				title: '',
				isleftarrow: '',
				good: {},
				logs: [],
				deliver:[],
				steps:[
					'下单',
					'支付',
					'发货',
					'评价'
				],
				addLogs: {}, //下单0
				payLogs: {}, //支付1
				tripLogs: {}, //发货2
				closeLogs: {}, //关闭-1

				extJson: {}, // 扩展对象
				marketing: {},
				orderInfo: {},
				goods: [],
				logistics: {},
				active: 1,
				hasSteps: false,
				visible: false,
				rateValue: 5,
				// reputation:2,   // 0 差评 1 中评 2 好评
				rateRemark: '', // 评价备注，限200字符
				closeTime: 0
			}
		},
		components: {
			topNav
		},
		created() {
			const id = this.$route.query.id;
			// console.log(id);
			if (!id) {
				this.$router.replace({
					path: '/home'
				});
			}
			this.getOrderDetail(id);
		},
		mounted() {
			this.title = this.$route.meta.title;
			this.isleftarrow = this.$route.meta.isleftarrow;
		},
		methods: {
			getOrderDetail(id) {
				this.$request.get('/order/detail', {
					token: util.storage.get('token'),
					id
				}).then(res => {
					console.log(res);
					this.orderInfo = res.data.orderInfo;
					if(this.orderInfo){
						this.good = res.data.goods;
						this.logs = res.data.logs;
						// 商品设置了物流模板才会有地址信息
						if (this.orderInfo.isNeedLogistics) {
							this.logistics = res.data.logistics
						}
						// 订单状态，-1 已关闭 0 待支付 1 已支付待发货 2 已发货待确认 3 确认收货待评价 4 已评价
						if (this.orderInfo.status > 0) {
							this.hasSteps = true
							this.active = this.orderInfo.status;
						}
						
						this.deliver = res.data.orderLogisticsShippers
						
						// 下单
						this.addLogs = res.data.logs.filter(item => item.type === 0)[0];
						// 支付
						this.payLogs = res.data.logs.filter(item => item.type === 1)[0];
						// 发货
						this.tripLogs = res.data.logs.filter(item => item.type === 2)[0];
						// 关闭
						this.closeLogs = res.data.logs.filter(item => item.type === -1)[0];
					}
				});
			},
			comfirmReceipt() {
				let orderId = this.$route.query.id;
				let token = util.storage.get('token');
				this.$dialog.confirm({
					title:'提示',
					message:'确认收货？\n<span style="color:red">提前收货造成损失，本商城将不会为您提供后续申述服务。</span>'
				}).then(()=>{
					// alert('确认收货')
					this.$request.post('/order/delivery',{
						orderId,token
					}).then(res=>{
						// console.log(res);
						if(res.code != 0 ){
							return;
						}
						this.$router.go(0);
					})
				}).catch(()=>{
					
				});
			},
			handleClipboard(order,event){
				// console.log(order,event);
				clipboard(order,event)
			}
		}
	}
</script>

